@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;600&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: 'Poppins', sans-serif;
	background: url('https://cdn.pixabay.com/photo/2015/03/26/09/47/sky-690293_960_720.jpg');
	background-repeat: no-repeat;
	background-size: cover;
	background-attachment: fixed;
	background-position: center;
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;
}

header h1 {
	font-size: 4rem;
	color: #fff;
	text-shadow: 2px 2px 5px #111;
	margin: 2rem 0;
}

.date-time {
	color: #fff;
	text-shadow: 2px 2px 5px #111;
	font-size: 1.5rem;
	margin-top: 2rem;
}

.error h1 {
	margin: 1rem;
	color: #fff;
	text-shadow: 2px 2px 5px #111;
}

@media (max-width: 600px) {
	header h1 {
		font-size: 3rem;
	}
}

.weather-search input {
	height: 55px;
	width: 300px;
	font-size: 1.5rem;
	padding: 0 2rem;
	margin: 2rem 0;
}

.weather-search input:focus::-webkit-input-placeholder {
	color: transparent;
}

.weather-search input:focus:-ms-input-placeholder {
	color: transparent;
}

.weather-search input:focus::placeholder {
	color: transparent;
}

.weather-search button {
	font-size: 1.3rem;
	font-weight: bold;
	font-size: 1.1.rem;
	cursor: pointer;
	padding: 0.75rem 2rem;
	border: 2px solid rgb(255, 255, 255);
	border-radius: 10px;
	background-color: rgba(0, 0, 0, 0.356);
	color: #fff;
	margin: 0 1rem;
	transition: all 0.5s ease;
}

.weather-search button:hover {
	background: transparent;
}

/*For smaller screen*/

@media (max-width: 600px) {
	.weather-search input {
		width: 90%;
		text-align: center;
		margin-top: 0;
	}
	.weather-search button {
		width: 70%;
	}
}

.weather-data {
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	border: 1px solid black;
	border-radius: 10px;
	background-color: rgb(2 39 58 / 66%);
	box-shadow: 5px 5px 25px #fff;
	margin: 2rem 0;
	padding-bottom: 2rem;
}

.weather-specs {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.weather-spec.wind {
	border-right: 1px solid black;
	border-left: 1px solid black;
}

.weather-spec .weather-title {
	margin: 1rem 3rem;
	font-size: 2.5rem;
	font-weight: 600;
	color: #fff;
	text-shadow: 2px 2px 5px #111;
}

.weather-spec .weather-value {
	font-size: 3rem;
	margin: 1rem 3rem;
	color: #fff;
	text-shadow: 2px 2px 5px #111;
}

.weather-spec .weather-value-sm {
	font-size: 1.5rem;
	margin: 1rem 3rem;
	color: #fff;
	text-shadow: 2px 2px 5px #111;
}

.city-name h1 {
	font-size: 4rem;
	color: #fff;
	text-shadow: 2px 2px 5px #111;
	margin: 1rem 0rem 2rem;
}

h1.description {
	font-size: 2rem;
	text-transform: capitalize;
}

/*For smaller screens*/

@media (max-width: 991px) {
	.weather-specs {
		flex-direction: column;
	}
	.weather-spec.wind {
		border: none;
	}
}

@media (max-width: 600px) {
	.weather-data {
		margin: 1rem 0.5rem;
	}
	.city-name h1 {
		font-size: 2rem;
	}
	h1.description {
		font-size: 1.5rem;
	}
	.weather-spec .weather-title {
		font-size: 1.5rem;
	}
	.weather-spec .weather-value {
		font-size: 1.4rem;
	}
}

